// Footer
#main-footer {
    background-color: white !important;
  }
  
  #uwo-footer {
    text-align: left;
    background-color: #444;
    clear: both;
    color: #444;
    font-size: 15px;
  }
  
  #footer-top {
    background-color: yellow;
    color: #444;
    text-align: center;
    font-weight: bold;
    font-size: 16px;
    padding: 12px 0;
  
    span {
      padding: 0 10px;
    }
  }
  
  // Begin our own grid system
  .grid-container {
    width: 100%;
  }
  
  .grid-container * {
    box-sizing: border-box;
  }
  // our cleafix hack
  .row:after,
  .row:before {
    content: "";
    display: table;
    clear: both;
  }
  
  .col {
    float: left;
    min-height: 1px;
    width: 25%;
    padding: 12px; // gutter
  }
  // Medium devices
  @media only screen and (max-width: 820px) {
    .col {
      width: 50%;
    }
  }
  // Small devices
  @media only screen and (max-width: 480px) {
    .col {
      width: 100%;
    }
  }
  // End our own grid system
  
  #footer-body {
    background-color: #444;
    padding: 45px 5%;
  
    a {
      color: #444;
      font-style: normal;
    }
  
    ul {
      list-style-type: none;
    }
  }
  
  #footer-column-1,
  #footer-column-2,
  #footer-column-3,
  #footer-column-4 {
    float: left;
    // height: 190px;
  }
  
  .footer-column-header {
    margin-bottom: 5px;
    font-weight: bold;
  }
  
  #footer-column-1 {
    .wismap {
      width: 40%;
    }
  
    p {
      padding-left: 40%;
      margin-left: 10px;
    }
  }
  
  #footer-address {
    width: 60%;
    float: right;
    padding-left: 10px;
  
    ul {
      line-height: 1.75em;
      margin: 0;
    }
  }
  
  #column-2-header {
    padding-left: 54px;
  }
  
  .quick-links {
    padding-bottom: 10px;
  }
  
  .quick-links::after {
    content: "";
    display: table;
    clear: both;
  }
  
  #footer-column-2,
  #footer-column-3 {
    padding-left: 60px;
  }
  
  #mobile-emergency {
    i {
      margin-right: 15px;
      float: left;
      width: 40px;
      text-align: center;
    }
  
    i:hover {
      color: yellow;
    }
  
    .fa-mobile {
      font-size: 60px;
    }
  
    .fa-exclamation-triangle {
      font-size: 40px;
      padding-top: 10px;
    }
  
    p {
      height: 65px;
      margin-bottom: 15px;
  
      a {
        vertical-align: sub;
        line-height: 1;
      }
    }
  }
  
  #sitemap {
    ul {
      margin: 0;
      line-height: 1.75;
  
      li:hover {
        text-decoration: underline;
      }
    }
  }
  
  #footer-column-4 {
    .image-wrapper {
      display: inline-block;
      padding-bottom: 10px;
  
      .accredited-img {
        padding-right: 10px;
      }
    }
  
    #footer-uw-logo {
      padding-right: 0;
    }
  }
  
  #footer-bottom {
    overflow: hidden;
    background-color: #444;
    padding: 0;
    width: 100%;
  
    #footer-copy {
      float: left;
      color: #444;
      font-size: 15px;
      padding-top: 19px;
      padding-left: 10px;
    }
  
    #footer-social {
      float: right;
  
      a {
        display: table-cell;
        text-align: center;
        color: #444;
        padding: 15px;
      }
    }
  }
  @media only screen and (max-width: 1025px) {
    #footer-column-1 {
      p {
        padding-left: 20px;
        margin-left: 0;
      }
  
      .wismap {
        display: none;
      }
  
      #footer-address {
        padding-left: 20px;
        width: 100%;
      }
    }
  
    #footer-column-2 {
      padding-left: 10px;
      padding-right: 30px;
    }
  
    #footer-column-3 {
      padding-left: 20px;
    }
  }
  @media only screen and (max-width: 640px) {
    // rules defined inside here are only applied to browsers that support CSS media queries and the browser window is 640px or smaller
    #footer-column-1,
    #footer-column-2,
    #footer-column-3,
    #footer-column-4 {
      text-align: left;
      padding: 0 0 15px;
  
      #footer-address,
      p {
        padding-left: 0;
      }
  
      ul {
        margin: 0;
      }
    }
  
    #footer-body {
      padding-bottom: 0;
    }
  
    #footer-column-1 {
      height: 160px;
    }
  
    #footer-column-2 p {
      width: 205px;
    }
  
    #footer-column-4 {
      img {
        padding: 35px 14% 35px 0;
      }
  
      #footer-accredited {
        padding-left: 0;
      }
    }
  
    #footer-bottom #footer-social a {
      padding: 15px 10px;
    }
  }
  @media only screen and (max-width: 540px) {
    #footer-bottom #footer-social {
      float: left;
    }
  }
  // END FOOTER